@charset "UTF-8";
.ai-features-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 24px;
  background: #ffffff;
  min-height: 100vh;
}

.header-section {
  margin-bottom: 32px;
}
.header-section .main-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 8px 0;
  line-height: 1.2;
}
.header-section .main-description {
  font-size: 1.125rem;
  color: #6b7280;
  margin: 0;
  line-height: 1.6;
}

.tabs-container {
  margin-bottom: 32px;
}
.tabs-container .tabs-nav {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
  gap: 0;
}
.tabs-container .tabs-nav .tab-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  border: none;
  background: transparent;
  color: #6b7280;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.2s ease;
}
.tabs-container .tabs-nav .tab-button:hover {
  color: #374151;
  background: #f9fafb;
}
.tabs-container .tabs-nav .tab-button.active {
  color: #3b82f6;
  border-bottom-color: #3b82f6;
  background: #f0f9ff;
}

.tab-panel .section-header {
  margin-bottom: 24px;
}
.tab-panel .section-header .section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 8px 0;
}
.tab-panel .section-header .section-description {
  color: #6b7280;
  margin: 0;
}

.upload-section {
  margin-bottom: 24px;
}
.upload-section .upload-area {
  background: #ffffff;
  border: 2px dashed #d1d5db;
  border-radius: 8px;
  padding: 60px 24px;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
}
.upload-section .upload-area:hover {
  border-color: #3b82f6;
  background: #f8fafc;
}
.upload-section .upload-area.dragover {
  border-color: #3b82f6;
  background: #f0f9ff;
}
.upload-section .upload-area .upload-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.upload-section .upload-area .upload-placeholder .upload-icon {
  color: #6b7280;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.upload-section .upload-area .upload-placeholder .upload-icon svg {
  width: 48px;
  height: 48px;
}
.upload-section .upload-area .upload-placeholder .upload-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 8px 0;
}
.upload-section .upload-area .upload-placeholder .upload-text {
  font-size: 1rem;
  font-weight: 400;
  color: #6b7280;
  margin: 0 0 8px 0;
}
.upload-section .upload-area .upload-placeholder .upload-hint {
  font-size: 0.875rem;
  color: #9ca3af;
  margin: 0;
  font-style: italic;
}
.upload-section .upload-area .single-image-preview {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.upload-section .upload-area .single-image-preview .preview-item {
  position: relative;
  display: inline-block;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}
.upload-section .upload-area .single-image-preview .preview-item .preview-image {
  max-width: 300px;
  max-height: 300px;
  object-fit: cover;
  display: block;
}
.upload-section .upload-area .single-image-preview .preview-item .preview-overlay {
  position: absolute;
  top: 8px;
  right: 8px;
}
.upload-section .upload-area .single-image-preview .preview-item .preview-overlay .remove-preview {
  background: rgba(239, 68, 68, 0.9);
  color: white;
  border: none;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s ease;
}
.upload-section .upload-area .single-image-preview .preview-item .preview-overlay .remove-preview:hover {
  background: rgba(220, 38, 38, 0.9);
}
.upload-section .upload-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
}
.upload-section .upload-actions .btn-secondary, .upload-section .upload-actions .btn-primary {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}
.upload-section .upload-actions .btn-secondary:disabled, .upload-section .upload-actions .btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.upload-section .upload-actions .btn-secondary {
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
}
.upload-section .upload-actions .btn-secondary:hover:not(:disabled) {
  background: #e5e7eb;
}
.upload-section .upload-actions .btn-primary {
  background: #3b82f6;
  color: white;
}
.upload-section .upload-actions .btn-primary:hover:not(:disabled) {
  background: #2563eb;
}

.supported-assets-section {
  margin-top: 32px;
}
.supported-assets-section .section-header {
  text-align: center;
  margin-bottom: 24px;
}
.supported-assets-section .section-header .section-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0 0 8px 0;
}
.supported-assets-section .section-header .section-description {
  font-size: 1rem;
  color: #6b7280;
  margin: 0;
  max-width: 600px;
  margin: 0 auto;
}
.supported-assets-section .asset-types-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 16px;
  margin-top: 24px;
}
.supported-assets-section .asset-types-grid .asset-type-card {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 20px;
  display: flex;
  align-items: center;
  gap: 16px;
  transition: all 0.2s ease;
  cursor: pointer;
}
.supported-assets-section .asset-types-grid .asset-type-card:hover {
  border-color: #3b82f6;
  box-shadow: 0 4px 6px -1px rgba(59, 130, 246, 0.1);
  transform: translateY(-2px);
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-icon-wrapper {
  flex-shrink: 0;
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-icon-wrapper .asset-type-icon {
  width: 48px;
  height: 48px;
  background: #f0f9ff;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #3b82f6;
  transition: all 0.2s ease;
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-type-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-type-info .asset-type-name {
  font-size: 1rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-type-info .asset-type-status {
  font-size: 0.875rem;
  color: #10b981;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 4px;
}
.supported-assets-section .asset-types-grid .asset-type-card .asset-type-info .asset-type-status::before {
  content: "âœ“";
  font-size: 0.75rem;
}
.supported-assets-section .asset-types-grid .asset-type-card:hover .asset-icon-wrapper .asset-type-icon {
  background: #3b82f6;
  color: #ffffff;
}

.file-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 24px;
}
.file-preview .preview-item {
  position: relative;
  width: 120px;
  height: 120px;
  border-radius: 8px;
  overflow: hidden;
  border: 2px solid #e5e7eb;
}
.file-preview .preview-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.file-preview .preview-item .remove-btn {
  position: absolute;
  top: 4px;
  right: 4px;
  background: rgba(239, 68, 68, 0.9);
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}
.file-preview .preview-item .remove-btn:hover {
  background: rgba(220, 38, 38, 0.9);
}

.analyze-button {
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 0 auto;
}
.analyze-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(59, 130, 246, 0.3);
}
.analyze-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.analyze-button .spinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  backdrop-filter: blur(4px);
}
.loading-overlay .loading-content {
  background: white;
  padding: 40px;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  max-width: 480px;
  width: 90%;
}
.loading-overlay .loading-content .loading-animation {
  margin-bottom: 24px;
}
.loading-overlay .loading-content .loading-animation .ai-brain {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
}
.loading-overlay .loading-content .loading-animation .ai-brain .brain-pulse {
  position: absolute;
  width: 100%;
  height: 100%;
  border: 3px solid #3b82f6;
  border-radius: 50%;
  animation: pulse 2s infinite;
}
.loading-overlay .loading-content .loading-animation .ai-brain .brain-pulse.delay-1 {
  animation-delay: 0.5s;
  border-color: #8b5cf6;
}
.loading-overlay .loading-content .loading-animation .ai-brain .brain-pulse.delay-2 {
  animation-delay: 1s;
  border-color: #06b6d4;
}
.loading-overlay .loading-content h3 {
  margin: 0 0 12px 0;
  color: #1f2937;
  font-size: 1.5rem;
  font-weight: 700;
}
.loading-overlay .loading-content .loading-description {
  margin: 0 0 24px 0;
  color: #6b7280;
  font-size: 1rem;
  line-height: 1.5;
}
.loading-overlay .loading-content .loading-steps {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
}
.loading-overlay .loading-content .loading-steps .step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 1;
  opacity: 0.4;
  transition: opacity 0.3s ease;
}
.loading-overlay .loading-content .loading-steps .step.active {
  opacity: 1;
}
.loading-overlay .loading-content .loading-steps .step .step-icon {
  font-size: 1.5rem;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f3f4f6;
  border-radius: 50%;
}
.loading-overlay .loading-content .loading-steps .step span {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.loading-overlay .loading-content .loading-tip {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 12px 16px;
  background: #f0f9ff;
  border: 1px solid #e0f2fe;
  border-radius: 8px;
  color: #0369a1;
  font-size: 0.875rem;
}
.loading-overlay .loading-content .loading-tip svg {
  color: #0ea5e9;
}

@keyframes pulse {
  0% {
    transform: scale(0.8);
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}
.recognition-results {
  margin-top: 24px;
}
.recognition-results .results-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 24px;
  color: white;
}
.recognition-results .results-header .header-content {
  display: flex;
  align-items: center;
  gap: 16px;
}
.recognition-results .results-header .header-content .header-icon {
  width: 48px;
  height: 48px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}
.recognition-results .results-header .header-content .header-icon svg {
  color: white;
}
.recognition-results .results-header .header-content .header-text h3 {
  margin: 0 0 4px 0;
  font-size: 1.5rem;
  font-weight: 700;
}
.recognition-results .results-header .header-content .header-text p {
  margin: 0;
  opacity: 0.9;
  font-size: 1rem;
}
.recognition-results .results-card, .recognition-results .recommendations-card, .recognition-results .feedback-card, .recognition-results .success-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  margin-bottom: 24px;
  overflow: hidden;
}
.recognition-results .results-card .card-header {
  background: #f8fafc;
  padding: 20px 24px;
  border-bottom: 1px solid #e2e8f0;
}
.recognition-results .results-card .card-header .asset-type {
  display: flex;
  align-items: center;
  gap: 16px;
}
.recognition-results .results-card .card-header .asset-type .type-icon {
  width: 48px;
  height: 48px;
  background: #3b82f6;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.recognition-results .results-card .card-header .asset-type .type-icon svg {
  color: white;
}
.recognition-results .results-card .card-header .asset-type .type-info h4 {
  margin: 0 0 4px 0;
  font-size: 1.25rem;
  font-weight: 700;
  color: #1e293b;
}
.recognition-results .results-card .card-header .asset-type .type-info .confidence-badge {
  display: inline-block;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.recognition-results .results-card .card-header .asset-type .type-info .confidence-badge.confidence-high {
  background: #dcfce7;
  color: #166534;
}
.recognition-results .results-card .card-header .asset-type .type-info .confidence-badge.confidence-medium {
  background: #fef3c7;
  color: #92400e;
}
.recognition-results .results-card .card-header .asset-type .type-info .confidence-badge.confidence-low {
  background: #fee2e2;
  color: #991b1b;
}
.recognition-results .results-card .card-content {
  padding: 24px;
}
.recognition-results .results-card .card-content .details-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 24px;
}
.recognition-results .results-card .card-content .details-grid .detail-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.875rem;
  font-weight: 600;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-label svg {
  color: #3b82f6;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value {
  font-size: 1rem;
  font-weight: 600;
  color: #1e293b;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value.condition-badge {
  display: inline-block;
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 600;
  text-align: center;
  max-width: fit-content;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value.condition-badge.condition-excellent {
  background: #dcfce7;
  color: #166534;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value.condition-badge.condition-good {
  background: #dbeafe;
  color: #1e40af;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value.condition-badge.condition-fair {
  background: #fef3c7;
  color: #92400e;
}
.recognition-results .results-card .card-content .details-grid .detail-item .detail-value.condition-badge.condition-poor {
  background: #fee2e2;
  color: #991b1b;
}
.recognition-results .results-card .card-content .description {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 16px;
}
.recognition-results .results-card .card-content .description .description-content {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}
.recognition-results .results-card .card-content .description .description-content svg {
  color: #3b82f6;
  margin-top: 2px;
  flex-shrink: 0;
}
.recognition-results .results-card .card-content .description .description-content p {
  margin: 0;
  color: #64748b;
  font-size: 0.875rem;
  line-height: 1.5;
}
.recognition-results .recommendations-card .card-header {
  background: #fef3c7;
  padding: 20px 24px;
  border-bottom: 1px solid #fde68a;
}
.recognition-results .recommendations-card .card-header .header-icon {
  width: 40px;
  height: 40px;
  background: #f59e0b;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.recognition-results .recommendations-card .card-header .header-icon svg {
  color: white;
}
.recognition-results .recommendations-card .card-header .header-text h4 {
  margin: 0 0 4px 0;
  font-size: 1.125rem;
  font-weight: 700;
  color: #92400e;
}
.recognition-results .recommendations-card .card-header .header-text p {
  margin: 0;
  color: #a16207;
  font-size: 0.875rem;
}
.recognition-results .recommendations-card .recommendations-list {
  padding: 24px;
}
.recognition-results .recommendations-card .recommendations-list .recommendation-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 0;
  border-bottom: 1px solid #f1f5f9;
}
.recognition-results .recommendations-card .recommendations-list .recommendation-item:last-child {
  border-bottom: none;
}
.recognition-results .recommendations-card .recommendations-list .recommendation-item .rec-icon {
  width: 24px;
  height: 24px;
  background: #dcfce7;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.recognition-results .recommendations-card .recommendations-list .recommendation-item .rec-icon svg {
  color: #16a34a;
}
.recognition-results .recommendations-card .recommendations-list .recommendation-item span {
  color: #374151;
  font-size: 0.875rem;
  line-height: 1.5;
}
.recognition-results .feedback-card .card-header {
  background: #eff6ff;
  padding: 20px 24px;
  border-bottom: 1px solid #dbeafe;
}
.recognition-results .feedback-card .card-header .header-icon {
  width: 40px;
  height: 40px;
  background: #3b82f6;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.recognition-results .feedback-card .card-header .header-icon svg {
  color: white;
}
.recognition-results .feedback-card .card-header .header-text h4 {
  margin: 0 0 4px 0;
  font-size: 1.125rem;
  font-weight: 700;
  color: #1e40af;
}
.recognition-results .feedback-card .card-header .header-text p {
  margin: 0;
  color: #3730a3;
  font-size: 0.875rem;
}
.recognition-results .feedback-card .feedback-content {
  padding: 24px;
}
.recognition-results .feedback-card .feedback-content .feedback-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  margin-bottom: 24px;
}
.recognition-results .feedback-card .feedback-content .feedback-options .feedback-btn {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px;
  border: 2px solid #e2e8f0;
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
}
.recognition-results .feedback-card .feedback-content .feedback-options .feedback-btn:hover {
  border-color: #3b82f6;
  background: #f8fafc;
}
.recognition-results .feedback-card .feedback-content .feedback-options .feedback-btn.selected {
  border-color: #3b82f6;
  background: #eff6ff;
}
.recognition-results .feedback-card .feedback-content .feedback-options .feedback-btn .btn-icon {
  width: 40px;
  height: 40px;
  background: #f1f5f9;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
